export default {
	data() {
		return {
			lists:[]
		}
	},
	methods: {
		
	},
	onLoad:function () {
		uni.startPullDownRefresh();
	},
	onReachBottom:function()
	{//上拉加载更多
		var i = this.lists.length;
		for (var i = 0; i < 2; i++) {
			this.lists.push(i+0);
		}
		
	},
	onPullDownRefresh:function () {
		//下拉刷新
		var all = [];
		for (var i = 0; i < 2; i++) {
			all[i] = i
		}
		this.lists=all;
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	}
}
